import { auth } from "@/lib/auth";
import Signout from "./Signout";

const Navbar = async () => {
  const session = await auth();

  return (
    <div className="bg-slate-950 p-6 text-slate-50 flex justify-end">
      {session?.user && <Signout />}
    </div>
  );
};

export default Navbar;
